import React from 'react'
import {Title} from "../../common/Components/title/Title";
import style from './Story.module.scss'
import {ContactItem} from "../../common/Components/contactItem/ContactItem";

export const Story = () => {
    return <div id={'story'} className={style.storyBlock}>

        <div className={style.storyContainer}>
            <div>
                <Title title={'About Me'}
                       subTitle={'MY STORY'}/>
            </div>
            <div className={style.descriptionBlock}>
                <div className={style.description}>
                    <p>  Frontend-     JavaScript- React  Redux.        TypeScript.        Redux Toolkit.        Frontend  (ReactNative)  Backend  (NodeJS).   .    .
                    </p>
                    <p>
                        I am engaged in Frontend development of user interfaces using React and Redux JavaScript libraries. I am well familiar with the strongly typed TypeScript programming language. Now I am studying a set of tools for effective development of the Redux Toolkit. I plan to develop and learn new technologies in Frontend development (ReactNative) and Backend development (Node JS). I'm developing my English. I take an active life position
                    </p>
                </div>
            </div>
            <div className={style.infoList}>
                <ul>
                    <li>
                        <ContactItem description={''} value={'34'}/>
                    </li>
                    <li>
                        <ContactItem description={''} value={'+7 912 753 97 00'}/>
                    </li>
                    <li>
                        <ContactItem description={''} value={', , Izhevsk'}/>
                    </li>
                    <li>
                        <ContactItem description={'E-Mail'} value={'e.sychev87 @yandex.ru'}/>
                    </li>
                </ul>
            </div>
        </div>


    </div>
}